.GalleryMainContent .content{display:flex;flex-direction:column;align-items:center;justify-content:center;padding:20px}.GalleryMainContent .content .galleryHead{display:flex;justify-content:center;flex-direction:column;gap:6px;align-items:center;margin-bottom:20px}.GalleryMainContent .content .galleryHead h1{font-size:24px;font-weight:600;line-height:1.2;color:#333}.GalleryMainContent .content .galleryHead hr{width:40px;height:2px;background:#333}.GalleryMainContent .content .gallery_card{display:grid;grid-template-columns:repeat(auto-fill, minmax(250px, 1fr));gap:20px;width:100%;max-width:1200px}.GalleryMainContent .content .gallery_card .galleryItem span{display:block;width:100%;height:0;padding-bottom:75%;position:relative;overflow:hidden;border-radius:10px;cursor:pointer}.GalleryMainContent .content .gallery_card .galleryItem span img{position:absolute;top:0;left:0;width:100%;max-width:100%;height:80%;object-fit:cover;transition:transform .3s ease}.GalleryMainContent .content .gallery_card .galleryItem span img:hover{transform:scale(1.05)}.GalleryMainContent .zoomedImageOverlay{position:fixed;top:0;left:0;width:100%;height:100%;background-color:rgba(0,0,0,.9);display:flex;flex-direction:column;justify-content:center;align-items:center;z-index:1000}.GalleryMainContent .zoomedImageWrapper{position:relative;width:90%;height:80%;max-width:1200px;max-height:800px}.GalleryMainContent .navButton{position:absolute;top:50%;transform:translateY(-50%);background:rgba(255,255,255,.3);border:none;border-radius:50%;width:40px;height:40px;display:flex;justify-content:center;align-items:center;cursor:pointer;transition:all .3s ease}.GalleryMainContent .navButton:hover{background:rgba(255,255,255,.5)}.GalleryMainContent .prevButton{left:10px}.GalleryMainContent .nextButton{right:10px}.GalleryMainContent .closeButton{position:absolute;top:10px;right:10px;background:rgba(255,255,255,.3);border:none;border-radius:50%;width:30px;height:30px;display:flex;justify-content:center;align-items:center;cursor:pointer;transition:.3s ease-in-out}.GalleryMainContent .closeButton:hover{background:rgba(255,255,255,.5)}.GalleryMainContent .imageCaption{color:#fff;text-align:center;margin-top:10px;font-size:14px;max-width:90%;padding:0 10px}@media(min-width: 320px){.GalleryMainContent .content .gallery_card{grid-template-columns:1fr 1fr;gap:15px}.GalleryMainContent .content .gallery_card .galleryItem img{width:100%;height:130px;max-width:320px}}@media(min-width: 360px){.GalleryMainContent .content .gallery_card{grid-template-columns:1fr 1fr;gap:15px}.GalleryMainContent .content .gallery_card .galleryItem img{width:100%;height:130px;max-width:320px}}@media(min-width: 375px){.GalleryMainContent .content .gallery_card{gap:20px}.GalleryMainContent .content .gallery_card .galleryItem img{width:198px;height:126px}}@media(min-width: 768px){.GalleryMainContent .content .galleryHead h1{font-size:24px}.GalleryMainContent .content .gallery_card{grid-template-columns:1fr 1fr 1fr;gap:24px}.GalleryMainContent .content .gallery_card .galleryItem img{width:234px;height:170px}.zoomedImageWrapper{width:95%;height:70%}.navButton{width:30px;height:30px}.closeButton{width:25px;height:25px}.imageCaption{font-size:12px}}@media(min-width: 992px){.GalleryMainContent .content .gallery_card{gap:28px}.GalleryMainContent .content .gallery_card .galleryItem img{width:300px;height:190px}.navButton{width:50px;height:50px}.closeButton{width:40px;height:40px}.imageCaption{font-size:16px}}@media(min-width: 1200px){.GalleryMainContent .content .gallery_card{grid-template-columns:1fr 1fr 1fr;gap:26px}.GalleryMainContent .content .gallery_card .galleryItem img{width:360px;height:250px}}